import PropTypes from "prop-types";
import React, { Fragment, useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import Rating from "./sub-components/ProductRating";
import { connect } from "react-redux";
import config from "../../config"
import Axios from "axios"

function MovieModal(props) {
  const [product, setProduct] = useState({});
  useEffect(() => {
    const { imdbID } = props.c_product;
    const URL = `http://${config.SEVER_ADDRESS}:${config.PORT}/${imdbID}`;
    Axios.post(URL).then((res) => {
        if(res.data.Response === "True") {
          setProduct(res.data);
        } else {
            alert(res.data.Error);
        }
    }).catch(error => {
        console.log(error);
    })
  }, [props.c_product])
  return (
    <Fragment>
      <Modal
        show={props.show}
        onHide={props.onHide}
        className="product-quickview-modal-wrapper"
      >
        <Modal.Header closeButton></Modal.Header>

        <div className="modal-body">
          <div className="row">
            <div className="col-md-5 col-sm-12 col-xs-12">
              <div className="product-large-image-wrapper text-center">
                <img
                  src={process.env.PUBLIC_URL + product.Poster === "N/A" ? `http://${config.SEVER_ADDRESS}:${config.PORT}/images/noposter.jpg` : process.env.PUBLIC_URL + product.Poster}
                  className="img-fluid border p-1"
                  alt=""
                />
              </div>
            </div>
            <div className="col-md-7 col-sm-12 col-xs-12">
              <div className="product-details-content quickview-content">
                <h2>{product.Title}</h2>
                <div className="product-details-price">
                  {product.Year}
                </div>
                <div className="pro-details-rating-wrap">
                  <div className="pro-details-rating">
                    <Rating ratingValue={parseInt(product.imdbRating) / 2} />
                  </div>
                </div>
                <div className="pro-details-list">
                  <p>{product.Plot}</p>
                  <hr />
                  <p>Released Time: {product.Released}</p>
                  <p>Run Time: {product.Runtime}</p>
                  <p>Genre: {product.Genre}</p>
                  <p>Director: {product.Director}</p>
                  <p>Writer: {product.Writer}</p>
                  <p>Actors: {product.Actors}</p>
                  <p>Language: {product.Language}</p>
                  <p>Country: {product.Country}</p>
                  <p>Awards: {product.Awards}</p>
                  <p>Votes: {product.imdbVotes}</p>
                  <p>Metascore: {product.Metascore}</p>
                  <p>Production: {product.Production}</p>
                  <p>Website: {product.Website}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Modal>
    </Fragment>
  );
}

MovieModal.propTypes = {
  onHide: PropTypes.func,
  product: PropTypes.object,
  show: PropTypes.bool,
  wishlistitem: PropTypes.object
};

const mapStateToProps = state => {
  return {
    cartitems: state.cartData
  };
};

export default connect(mapStateToProps)(MovieModal);
