import PropTypes from "prop-types";
import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { useToasts } from "react-toast-notifications";
import Rating from "./sub-components/ProductRating";
import MovieModal from "./MovieModal";
import config from "../../config";
const MovieGridListSingle = ({
  product,
  addToWishlist,
  wishlistItem,
  sliderClassName,
  spaceBottomClass
}) => {
  const [modalShow, setModalShow] = useState(false);
  const { addToast } = useToasts();

  return (
    <Fragment>
      <div
        
        className={`col-xl-4 col-sm-6 ${
          sliderClassName ? sliderClassName : ""
        }`}
        
      >
        <div
        style = {{ boxShadow: "0 3px 6px 2px rgba(0,0,0,0.2)", padding: "10px 10px 20px 10px", borderRadius: "5px", backgroundColor: "#fef6a6"}} 
          className={`product-wrap ${spaceBottomClass ? spaceBottomClass : ""}`}
        >
          <div className="product-img">
            <Link to={process.env.PUBLIC_URL + "/" + product.imdbID}>
              <img
                style={{border: "1px  grey solid", borderRadius: "2px"}}
                className="img-fluid"
                // src={process.env.PUBLIC_URL + product.Poster}
                src={process.env.PUBLIC_URL + product.Poster === "N/A" ? `http://${config.SEVER_ADDRESS}:${config.PORT}/images/noposter.jpg` : process.env.PUBLIC_URL + product.Poster}
                alt=""
              />
              {/* <img
                className="hover-img"
                src={process.env.PUBLIC_URL + product.Poster}
                alt=""
              /> */}
            </Link>
            
            <div className="product-action">
              <div className="pro-same-action pro-wishlist">
                <button
                  className={wishlistItem !== undefined ? "active" : ""}
                  disabled={wishlistItem !== undefined}
                  title={
                    wishlistItem !== undefined
                      ? "Added to wishlist"
                      : "Add to wishlist"
                  }
                  onClick={() => addToWishlist(product, addToast)}
                >
                  <i className="pe-7s-like" />
                </button>
              </div>
              <div className="pro-same-action pro-cart">
                <Link to={`${process.env.PUBLIC_URL}/${product.imdbID}`}>
                  More...
                </Link>
              </div>
              <div className="pro-same-action pro-quickview">
                <button onClick={() => setModalShow(true)} title="Quick View">
                  <i className="pe-7s-look" />
                </button>
              </div>
            </div>
          </div>
          <hr style={{margin:"0"}}/>
          <div className="product-content text-center">
            <h3>
              <Link to={process.env.PUBLIC_URL + "/" + product.imdbID}>
                {product.Title.length > 22 ? product.Title.slice(0, 22) + "...": product.Title}
              </Link>
            </h3>
            {parseInt(product.imdbRating) && parseInt(product.imdbRating) > 0 ? (
              <div className="product-rating">
                <Rating ratingValue={parseInt(product.imdbRating) / 2} />
              </div>
            ) : (
              ""
            )}
            <div className="product-price">
              {product.Year}
            </div>
          </div>
        </div>
      </div>
      {/* product modal */}
      <MovieModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        c_product={product}
      />
    </Fragment>
  );
};

MovieGridListSingle.propTypes = {
  addToWishlist: PropTypes.func,
  product: PropTypes.object,
  sliderClassName: PropTypes.string,
  spaceBottomClass: PropTypes.string,
  wishlistItem: PropTypes.object
};

export default MovieGridListSingle;
