import React,  {useState} from "react";

const MovieSearch = ({search, type}) => {
  const [isDisabled, setIsDisabled] = useState(true);
  const [inputValue, setInputValue] = useState("");
  const handleChange = (e) => {
    setIsDisabled((e.target.value === ""));
    setInputValue(e.target.value);
  };
  const onSearch = (e) => {
    e.preventDefault();
    search({title: inputValue, type})
  }
  return (
    <div className="sidebar-widget">
      <h4 className="pro-sidebar-title">Search </h4>
      <div className="pro-sidebar-search mb-50 mt-25">
        <form className="pro-sidebar-search-form" action="#" onSubmit={onSearch}>
          <input type="text" placeholder="Search here..." value={inputValue} onChange={handleChange}/>
          <button className={isDisabled? "d_button" : ""} disabled={isDisabled}><i className="pe-7s-search"></i></button>
        </form>
      </div>
    </div>
    
  );
};

export default MovieSearch;
