import PropTypes from "prop-types";
import React from "react";
import MovieSearch from "../../components/product/MovieSearch";
import MovieType from "../../components/product/MovieType";

const MovieSidebar = ({ search, type, getSearchType, sideSpaceClass }) => {
  return (
    <div className={`sidebar-style ${sideSpaceClass ? sideSpaceClass : ""}`}>
      <MovieSearch search={search} type={type} />
      <MovieType types={['movie', 'series', 'episode']} getSearchType={getSearchType} />
    </div>
  );
};

MovieSidebar.propTypes = {
  getSearchValue: PropTypes.func,
  sideSpaceClass: PropTypes.string
};

export default MovieSidebar;
