import PropTypes from "prop-types";
import React, { Fragment, useState, useEffect } from 'react';
import MetaTags from 'react-meta-tags';
import Paginator from 'react-hooks-paginator';
import { connect } from 'react-redux';
import LayoutOne from '../layouts/LayoutOne';
import MovieSidebar from '../wrappers/product/MovieSidebar';
import MovieTopbar from '../wrappers/product/MovieTopbar';
import MovieItems from '../wrappers/product/MovieItems';
import { fetchProducts } from "../redux/actions/productActions";
import { fetchPendFlag } from "../redux/actions/pendActions";
import Axios from "axios";
import {useHistory} from 'react-router-dom';
import config from '../../src/config';

const MovieList = ({products, setSearchData, setPendFlag}) => {
    const history = useHistory();
    const [layout, setLayout] = useState('grid three-column');
    const [movieType, setMovieType] = useState('');
    const [offset, setOffset] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [currentData, setCurrentData] = useState([]);
    const pageLimit = 6;
    
    const getLayout = (layout) => {
        setLayout(layout)
    }
    const getSearchType = (value) => {
        setMovieType(value);
    }
    const search = (data) => {
        setPendFlag(true);
        const URL = `http://${config.SEVER_ADDRESS}:${config.PORT}/?title=${data.title}&type=${data.type}`;
        history.push(`?q=${data.title}&type=${data.type}`);
        Axios.post(URL).then((res) => {
            if(res.data.Response === "True") {
                setSearchData(res.data.Search)
            } else {
                alert(res.data.Error);
            }
            setPendFlag(false);
        }).catch(error => {
            console.log(error);
            setPendFlag(false);
        })
    }
    useEffect(() => {
        let params = (new URL(document.location)).searchParams;
        let title = params.get("q");
        let type = params.get("type");
        if(title !== null | type !== null) {
            const URL = `http://${config.SEVER_ADDRESS}:${config.PORT}/?title=${title}&type=${type}`;
            Axios.post(URL).then((res) => {
                if(res.data.Response === "True") {
                    setSearchData(res.data.Search)
                } else {
                    alert(res.data.Error);
                }
            }).catch(error => {
                console.log(error);
            })
        }
    }, [setSearchData])
    useEffect(() => {
        setCurrentData(products.slice(offset, offset + pageLimit));
    }, [offset, products, pageLimit]);
    return (
        <Fragment>
            <MetaTags>
                <title>MDB-API Page</title>
                <meta name="description" content="MDB API PAGE" />
            </MetaTags>

            <LayoutOne headerTop="visible">

                <div className="shop-area pt-95 pb-100">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3 order-2 order-lg-1">
                                <MovieSidebar search={search} type={movieType} getSearchType={getSearchType} sideSpaceClass="mr-30"/>
                            </div>
                            <div className="col-lg-9 order-1 order-lg-2">
                                <MovieTopbar getLayout={getLayout} productCount={products.length} sortedProductCount={currentData.length} />
                                <MovieItems layout={layout} products={currentData} />
                                <div className="pro-pagination-style text-center mt-30">
                                    <Paginator
                                        totalRecords={products.length}
                                        pageLimit={pageLimit}
                                        pageNeighbours={2}
                                        setOffset={setOffset}
                                        currentPage={currentPage}
                                        setCurrentPage={setCurrentPage}
                                        pageContainerClass="mb-0 mt-0"
                                        pagePrevText="Â«"
                                        pageNextText="Â»"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </LayoutOne>
        </Fragment>
    )
}

MovieList.propTypes = {
  location: PropTypes.object,
  products: PropTypes.array,
  setSearchData: PropTypes.func,
  setPendFlag: PropTypes.func
}

const mapStateToProps = state => {
    return{
        products: state.productData.products
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setSearchData: (movie) => {
            dispatch(fetchProducts(movie));
        },
        setPendFlag: (flag) => {
            dispatch(fetchPendFlag(flag));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MovieList);