import PropTypes from "prop-types";
import React from "react";
import { setActiveSort } from "../../helpers/product";

const MovieType = ({ types, getSearchType }) => {
  return (
    <div className="sidebar-widget">
      <h4 className="pro-sidebar-title">Types </h4>
      <div className="sidebar-widget-list mt-30">
        {types ? (
          <ul>
            <li>
              <div className="sidebar-widget-list-left">
                <button
                  onClick={e => {
                    getSearchType("");
                    setActiveSort(e);
                  }}
                >
                  <span className="checkmark" /> All Types
                </button>
              </div>
            </li>
            {types.map((type, key) => {
              return (
                <li key={key}>
                  <div className="sidebar-widget-list-left">
                    <button
                      onClick={e => {
                        getSearchType(type);
                        setActiveSort(e);
                      }}
                    >
                      {" "}
                      <span className="checkmark" /> {type}{" "}
                    </button>
                  </div>
                </li>
              );
            })}
          </ul>
        ) : (
          "No categories found"
        )}
      </div>
    </div>
  );
};

MovieType.propTypes = {
  type: PropTypes.array,
  getSearchType: PropTypes.func
};

export default MovieType;
